body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header_color {
  background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #0f2027,
    #203a43,
    #2c5364
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing_grid {
  background: #0f2027; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #0f2027,
    #203a43,
    #2c5364
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing_grid img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
}

.banner_text {
  opacity: 0.8;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 75%;
  margin: auto;
  border-radius: 10px;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
}

.banner_text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner_text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner_text p {
  font-size: 20px;
  font-weight: bold;
  color: white;
  padding: 1em;
}

.social_links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 50%;
  margin: auto;
}

.social_links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

